// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import axios from 'axios';
import Buefy from 'buefy';
import 'buefy/dist/buefy.css';
import App from './App';
import router from './router';

Vue.config.productionTip = false;

Vue.use(Buefy);

axios.get(process.env.API_REST_URL + 'furet-ui/app/component/files')
  .then((res) => {
    res.data.js.forEach((file) => {
      const script = document.createElement('script');// create a script DOM node
      script.src = process.env.API_REST_URL + file;// set its src to the provided URL
      document.head.appendChild(script);
    });
    res.data.css.forEach((file) => {
      const style = document.createElement('link');// create a stylesheet DOM node
      style.href = process.env.API_REST_URL + file;// set its src to the provided URL
      style.rel = 'stylesheet';
      style.type = 'text/css';
      document.head.appendChild(style);
    });

    /* eslint-disable no-new */
    new Vue({
      el: '#furet-ui-app',
      router,
      components: { App },
      template: '<App/>',
    });
  })
  .catch((error) => {
    // eslint-disable-next-line
    console.error(error);
  });
/* eslint-disable no-new */
new Vue({
  el: '#furet-ui-app',
  router,
  components: { App },
  template: '<App/>',
});



